import React, { useEffect, useState } from "react";
import styled from "styled-components";
import CurrencyInput from "../../components/CurrencyInput";
import { ButtonConfirmed, ButtonPrimary, PendingContent } from "../../components/Button";
import { getBalanceNumber, getDisplayBalance, getFullDisplayBalance } from "../../utils/formatBalance";
import { useWeb3React } from "@web3-react/core";
import { MarketGenerationService } from "../../services/MarketGenerationService";
import { RowBetween, RowFixed } from "../../components/Row";
import { ErrorMessage } from "../../components/ErrorMessage";
import { extractErrorMessage } from "../../utils/extractErrorMessage";
import TransactionCompletedModal from "../../components/TransactionCompletedModal";
import { Option } from "../../components/Button";
import { TokenService } from "../../services/TokenService";
import { BASE_ADDRESS, BASE_DECIMALS, MARKET_GENERATION_ADDRESS } from "../../constants";
import useTokenBalance from "../../hooks/useTokenBalance";
import { useParams } from "react-router-dom";
import { decrypt } from "../../utils/encrypt";
import { ReferralLink } from "../ReferralLink";
import { ExternalLink } from "../Link";
import { lighten } from "polished";

const Subtitle = styled.div`
  padding: 0.25em;
  font-size: 0.875rem;
  text-align: center;
`;

const ButtonsWrapper = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1.5em;
`;

const BuyRoudLink = styled(ExternalLink)`
    
    font-size: 0.875em;
    color: ${({ theme }) => theme.primary1};

    :hover {
        text-decoration: none;
        color: ${({ theme }) => lighten(0.2, theme.primary1)};
      }
`
const BuyRoudLinkWrapper = styled.div`
      text-align: center;
`

enum ContributionStatus {
    None,
    Approving,
    Approved,
    Contributing,
    Contributed
}

export const Contribute = ({onContribute} : { onContribute: () => void}) => {
    const { account, library } = useWeb3React();
    const [status, setStatus] = useState<ContributionStatus>(ContributionStatus.None);
    const [value, setValue] = useState<string>("");
    const [error, setError] = useState("");
    const [transactionHash, setTransactionHash] = useState<string>("");
    const [round, setRound] = useState<number>(0);
    const [isApproved, setIsApproved] = useState<boolean>(false);
    const balance = useTokenBalance(BASE_ADDRESS);
    const { ref } = useParams<{ ref: string }>();
    const referralAddress = ref ? decrypt(ref) : undefined;
    
    useEffect(() => {
        const getIsApprove = async () => {
            const service = new TokenService(library, account!, BASE_ADDRESS);
            const approved = await service.isApproved(MARKET_GENERATION_ADDRESS);
            setIsApproved(approved);
           
            if(approved)
            {
                setStatus(ContributionStatus.Approved);               
            }
        }
        getIsApprove();
    }, [library, account])

    const IsValueValid = () =>{
        const amount = parseFloat(value);
        return !Number.isNaN(amount) && amount > 0;
    }

    const approve = async () => {
         try {
            setStatus(ContributionStatus.Approving);
            const service = new TokenService(library, account!, BASE_ADDRESS);
            const txResponse = await service.approve(MARKET_GENERATION_ADDRESS);
            if (txResponse) {
                const receipt = await txResponse.wait()
                if (receipt?.status === 1) {
                    setTransactionHash(receipt.transactionHash);
                    
                }
                else {
                    setError("Transaction Failed")
                }
            }
            setStatus(ContributionStatus.Approved);
        }
        catch (e) {
            console.log(e);
            const errorMessage = extractErrorMessage(e);
            if(errorMessage) {
                setError(errorMessage);
            }
            setStatus(ContributionStatus.None);
        }
    }

    const contribute = async () => {
       
        if (!IsValueValid())
        {
            setError("Enter Contribution");
            return;
        }

        if (round === 0)
        {
            setError("Select Buy Round");
            return;
        }

        setError("");

        try {
            setStatus(ContributionStatus.Contributing);
            const service = new MarketGenerationService(library, account!)
            const txResponse = await service.contribute(value, round, referralAddress)

            if (txResponse) {
                const receipt = await txResponse.wait()
                if (receipt?.status === 1) {
                    setTransactionHash(receipt.transactionHash);                       
                }
                else {
                    setError("Transaction Failed")
                }
            }
            setStatus(ContributionStatus.Contributed);
            setValue("");
            setRound(0);
            onContribute();
        }
        catch (e) {
            console.log(e)
            const errorMessage = extractErrorMessage(e);
            if(errorMessage) {
                setError(errorMessage);
            }
            setStatus(ContributionStatus.Approved)
        }
    }

    return (
        <>
            <TransactionCompletedModal title={"Contribution Sent"} hash={transactionHash} isOpen={status === ContributionStatus.Contributed} onDismiss={() => setStatus(ContributionStatus.None)} />
            <CurrencyInput
                value={value}
                balance={getDisplayBalance(balance, 2, BASE_DECIMALS)}
                numericBalance={getBalanceNumber(balance, BASE_DECIMALS)}
                onSubmit={contribute}
                ticker={"USDT"}
                label={"Contribution"}
                onMax={() => setValue(getFullDisplayBalance(balance, BASE_DECIMALS))}
                showMaxButton={true}
                onUserInput={setValue}
                id={"ContributionInput"}
            />           
            <RowFixed>
                <Subtitle>Buy round</Subtitle>
                {/* <QuestionHelper text="Your transaction will revert if the price changes unfavorably by more than this percentage." /> */}
            </RowFixed>
            <RowBetween>
                <Option onClick={() => setRound(1)} active={round === 1}>1</Option>
                <Option onClick={() => setRound(2)} active={round === 2}>2</Option>
                <Option onClick={() => setRound(3)} active={round === 3}>3</Option>
            </RowBetween>
            <BuyRoudLinkWrapper>
                <BuyRoudLink href="https://rootkitfinance.medium.com/market-generation-event-93945b5e529f">Learn more about the buy rounds here</BuyRoudLink>
            </BuyRoudLinkWrapper>
            {isApproved 
                ? 
                <ButtonPrimary disabled={status !== ContributionStatus.Approved} onClick={contribute}>
                    {status === ContributionStatus.Contributing
                        ? <PendingContent text={"Pending..."}/>
                        : "Contribute"
                    }
                </ButtonPrimary> 
                :
                 <ButtonsWrapper>
                    <ButtonConfirmed confirmed={status === ContributionStatus.Approved} onClick={approve}>
                        {status === ContributionStatus.Approving
                            ? <PendingContent text={"Approving..."}/>
                            : status === ContributionStatus.Approved ? "Approved" : "Approve"
                        }
                    </ButtonConfirmed>
                    <ButtonPrimary disabled={status !== ContributionStatus.Approved} onClick={contribute}>
                        {status === ContributionStatus.Contributing
                            ? <PendingContent text={"Pending..."}/>
                            : "Contribute"
                        }
                    </ButtonPrimary>
                </ButtonsWrapper>
                 }
           
            {error ? <ErrorMessage error={error} /> : null}
            
            <ReferralLink address={account!}/>              
        </>
    );
};
